import React from "react";
import { Link } from "react-router-dom";
import GoogleMapReact from "google-map-react"
import PropTypes from 'prop-types';
// import superCluster from 'points-cluster';

const markerStyle = {
    position: "absolute",
    top: "100%",
    left: "50%",
    transform: "translate(-50%, -100%)"
};

class SimpleMap extends React.Component {

    static defaultProps = {
        handleMarkerClick: undefined,
        center: {
            lat: 53.5,
            lng: -2.3
        },
        zoom: 12
    };

    constructor(props) {
        super(props);
        this.state = {
            mapOptions: {
                
            },
            clusters: []
        };
    }

    handleMarkerClick(location) {
        if (undefined !== this.props.handleMarkerClick) {
            this.props.handleMarkerClick(location);
        }
    }

    // getClusters = () => {
    //     const clusters = supercluster(markersData, {
    //       minZoom: 0,
    //       maxZoom: 16,
    //       radius: 60,
    //     });

    //     return clusters(this.state.mapOptions);
    //   };

    // createClusters = props => {
    //     console.log(`createClusters`, props);
    //     this.setState({
    //         clusters: this.state.mapOptions.bounds
    //             ? this.getClusters(props).map(({ wx, wy, numPoints, points }) => ({
    //                 lat: wy,
    //                 lng: wx,
    //                 numPoints,
    //                 id: `${numPoints}_${points[0].id}`,
    //                 points,
    //             }))
    //             : [],
    //     });
    // };

    handleMapChange = ({ center, zoom, bounds }) => {
        console.log(`handleMapChange`, center, zoom, bounds);
        this.setState({
            mapOptions: { center, zoom, bounds }
        }, () => {
            console.log('handleMapChange setState done', center, zoom, bounds, this.props, this.state);
        });

        // this.setState(
        //     {
        //         mapOptions: {
        //             center,
        //             zoom,
        //             bounds,
        //         },
        //     },
        //     () => {
        //         this.createClusters(this.props);
        //     }
        // );
    };

    render() {
        return (
            // Important! Always set the container height explicitly
            <div style={{ height: "100vh", width: "100%" }}>

                <GoogleMapReact
                    bootstrapURLKeys={{ key: "AIzaSyBeQJKcfrMZkXf3kRJZZkD34V3rnM-IgYI" /* https://console.cloud.google.com/google/maps-apis/credentials?project=hvpdjsoft */ }}
                    defaultCenter={this.props.center}
                    defaultZoom={this.props.zoom}
                    onChange={this.handleMapChange}
                >


                    {this.props.locations.map(item => {
                        return (
                            <Link to={"/"} lat={item.lat} lng={item.lng}>
                                <img style={markerStyle} src={process.env.PUBLIC_URL + '/' + item.icon} alt="pin" onClick={() => this.handleMarkerClick(item)} />
                            </Link>
                        );
                    })}

                </GoogleMapReact>
            </div>
        );
    }
}

SimpleMap.propTypes = {
    handleMarkerClick: PropTypes.func,
    zoom: PropTypes.number,
    center: PropTypes.object
};

export default SimpleMap;